@keyframes move-to-right {
    0% {
        transform: translateX(-100%);
    }

    100% {
        transform: translateX(0%);
    }
}

@keyframes move-to-left {
    0% {
        transform: translateX(100%);
    }

    100% {
        transform: translateX(0%);
    }
}

.slider {
    display: flex;
    background-color: #000000; 
    flex-grow: 1;
    min-height: 85%;
}

.slider .wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.slider__slides {  
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    flex-grow: 1;
    padding-top: 25px;
    padding-bottom: 20px;
    overflow: hidden;
}

.slider__item {
    display: none;
    flex-direction: column;
    justify-content: center; 
    
    & img {
        opacity: 0.5;
        border-radius: 8px;
        margin-left: 50px;
        height: 100%;
    }
}

.slider__description {
    z-index: 1;
    position: absolute;
    bottom: 0;
    left: 0;
}

.slider__item--active {    
    display: flex;
    position: absolute;
    top: 25px;
    left: 0;
    height: 90%;
}

.slider__title {
    @include font-param(60px, 50px, bold);
    margin-bottom: 20px;
}

.slider__button {
    @include font-param(14px, 16px, normal);
    color: #FF781C;
}

.slider__icon {
    position: absolute;
    top: 20px;
    left: 0;
    padding-left: 30px;
    z-index: 3;

    @include font-param(16px, 19px, normal);

    &::before {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 15px;
        height: 15px;
        background-repeat: no-repeat;
        background-position: center;        
    }
}

.slider__icon--hottest::before {
    background-image: url("../img/hottest-icon.svg");
}

.slider__icon--nearest::before {
    background-image: url("../img/nearest-icon.svg");
}

.slider__icon--tour::before {
    background-image: url("../img/tour-icon.svg");
}

.slider__controls {
    padding-top: 17px;
    padding-bottom: 17px;
    display: flex;
    justify-content: center;
}

.slider__dot {
    display: block;
    width: 6px;
    height: 6px;
    background-color: #333333;
    border-radius: 50%;

    &:not(:last-child) {
        margin-right: 6px;
    }
}

.slider__dot--active {
    background-color: #FF781C;
}
